
import { defineComponent, ref } from "vue";
import { Icon } from "@iconify/vue";

export default defineComponent({
  props: {
    cmp: {
      type: String,
      required: true,
    },
  },
  components: { Icon },
  setup(props) {
    const showCode = ref(false);
    return {
      showCode,
      changeShowCode() {
        showCode.value = !showCode.value;
      },
    };
  },
});
