
  <div class="effect-code">
    <component :is="cmp" />
    <hr class="myhr" />
    <div class="utils">
      <a-tooltip>
        <template #title>复制代码</template>
        <Icon icon="cil:copy" />
      </a-tooltip>
      <a-tooltip>
        <template #title>显示代码</template>
        <Icon icon="bi:code-slash" @click="changeShowCode" />
      </a-tooltip>
    </div>

    <div v-show="showCode">
      <slot name="code" />
    </div>
  </div>
